import React from 'react';
import {Card,Label } from 'semantic-ui-react';

const ServerCard = props => {
    return(
        <Card color={props.color} href={props.to}>
            <Card.Content>
              <Card.Header>{props.hostname}</Card.Header>
              <Card.Description>
                {props.plugin_output}
              </Card.Description>
              </Card.Content>
              <Card.Content extra>
                <Label color={props.color} circular floating>
                    {props.state}
                </Label>
            </Card.Content>
        </Card>
    );
}

export default ServerCard;