import React, { Component } from 'react';
import {NavLink} from 'react-router-dom';
import { Menu,Icon } from 'semantic-ui-react';

class Navbar extends Component {
  state = {}

  handleItemClick = (e, { name }) => this.setState({ activeItem: name })

  render() {
    const { activeItem } = this.state

    return (
      <Menu pointing secondary size='massive'>

        <Menu.Item >
          <img src='https://www.bemlindia.in/images/logo_beml1.png'/>
        </Menu.Item>

        <Menu.Item header
        name='Bharat Earth Movers Limited'
        />

        <Menu.Menu position='left'>
            <Menu.Item as={NavLink} to='Hosts' color='red'
            name='Hosts'
            active={activeItem === 'Hosts'}
            >
              <Icon name='server'/>Hosts
            </Menu.Item>
            <Menu.Item as={NavLink} to='Services' color='green'
            name='Services'
            active={activeItem === 'Services'}
            />
            <Menu.Item as={NavLink} to='Analytics' color='blue'
            name='Analytics'
            active={activeItem === 'Analytics'}
            />
        </Menu.Menu>

        <Menu.Menu position='right'>

            <Menu.Item as={NavLink} to='About' color='blue'
            name='About'
            active={activeItem === 'About'}
            />
            
        </Menu.Menu>

      </Menu>
    )
  }
}
export default Navbar;