import React, { Component } from 'react';
import axios from 'axios';
import {Message,Grid,Icon } from 'semantic-ui-react';
import ServerCard from './ServerCard';

//const API_URL = 'http://18.219.235.189/status.php';
const API_URL = 'https://rohit1727.github.io/server_log/status.json';

class Hosts extends Component{
  constructor(){
    super();
    this.state={
        hosts:{},
        loading: true,
        error:null,
      }
      this.getData = this.getData.bind(this);
    }

    getData() {
      axios
        .get(`${API_URL}`)
        .then(response => this.setState({hosts: response.data.hosts,loading:false}))
        .catch(error => this.setState({ error, loading: false }));
    }

componentDidMount() {
  this.getData()
  setInterval(this.getData, 2000)
}

  render(){
    const data = Object.keys(this.state.hosts).map((key) => (
        
      this.state.hosts[key].current_state == 1 ?(
        <ServerCard 
          color='green'
          to='/#/Services'
          hostname={this.state.hosts[key].host_name}
          plugin_output={this.state.hosts[key].plugin_output}
          state='UP'
          />
          
        
      ):
      (
        <ServerCard 
          color='red'
          to='/#/Analytics'
          hostname={this.state.hosts[key].host_name}
          plugin_output={this.state.hosts[key].plugin_output}
          state='DOWN'
          />
      )
  ));

  var d1 = Object.keys(data).map(function(key) {
    return <Grid.Column value={key}>{data[key]}</Grid.Column>
  });
    
    return(
      this.state.loading?(
        <Message icon>
          <Icon name='circle notched' loading />
          <Message.Content>
            <Message.Header>Just one second</Message.Header>
            We are fetching that content for you.
          </Message.Content>
        </Message>
      ):(
           <Grid padded columns={3}>
                {d1}
            </Grid>
      )
      
      );
    }
  }  
  export default Hosts;